import {
  createApp
} from 'vue'
import App from './App.vue'
import router from "./router";
import store from "./store";
import Vant from 'vant';
import 'vant/lib/index.css';
import {
  intro,
  locale
} from "./storage"
import {
  i18n,
  setLang
} from "./utils/i18n"
import dateformat from "dateformat"
const lang = locale.get() || 'zh-TW'
import { Toast } from "vant";
setLang(lang)
// 返回上一页
const goBack = () => {
  if (history.length > 0) {
    router.go(-1)
  } else {
    router.push({
      name: 'Welcome'
    })
  }
}
let queryParse = (str) => {
  if (!str || str === '0') {
    return {}
  }
  let dataArr = decodeURIComponent(str).split('&')
  let params = {}
  dataArr.forEach(query => {
    let queryItem = query.split('=')
    if (queryItem.length === 1) {
      params.id = queryItem[0]
    } else {
      params[queryItem[0]] = queryItem[1]
    }
  })
  return params
}

let href = window.location.href.replace(/#/g, '').replace(/\?utm_source=tokenpocket/g, '')
if (href.includes('?')) {
  let search = href.slice(href.indexOf('?') + 1, href.length)
  let params = queryParse(search)
  if (params.intro) {
    // 是否有推荐邀请码
    intro.set(params.intro)
  }
}

const errorformat = (error, callback) => {
  console.log(error);
  let errorStr = error.toString();
  const t = i18n.global.t;
  if (errorStr.indexOf("gas not enough") !== -1) {
    return Toast(t("index.gas not enough"));
  }
  if (errorStr.indexOf("cannot select this stake mode") !== -1) {
    return Toast(t("index.复投不能選擇該方案"));
  }
  if (errorStr.indexOf("caller is not the owner") !== -1) {
    return Toast(t("caller is not the owner"));
  }
  if (errorStr.indexOf("No referrer") !== -1) {
    return Toast(t("index.沒有綁定推薦人"));
  }
  if (errorStr.indexOf("Already bound") !== -1) {
    return Toast(t("index.Already bound"));
  }
  if (errorStr.indexOf("transfer amount exceeds balance") !== -1) {
    return Toast(t("index.餘額不足"));
  }
  if (errorStr.indexOf("Referrer not valid") !== -1) {
    return Toast(t("index.該地址無質押，不能進行綁定"));
  }

  if (typeof callback === "object") {
    callback();
  } else {
    Toast(t("index.Fail"));
  }
};

const app = createApp(App);
app.config.globalProperties.$goBack = goBack;
app.config.globalProperties.$dateformat = dateformat;
app.config.globalProperties.$errorformat = errorformat;
app.config.globalProperties.$setErrorImg = (e) => {
  e.target.src = require('@/assets/images/not_found.png')
};

app
  .use(router)
  .use(store)
  .use(i18n)
  .use(Vant)
  .mount('#app');
