import {
  createRouter,
  createWebHashHistory
} from "vue-router";

const routes = [{
  path: '/',
  name: 'Index',
  component: () => import('@/views/index'),
  meta: {
    title: '',
    // keepAlive: true
  }
}]

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

router.beforeEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
})

export default router;
